import React, {Component} from 'react';
import {Button, Table, Row, Col, Icon} from "antd";
import {Link} from "react-router-dom";

const {Column, ColumnGroup} = Table;

class Home extends Component {
    state = {
        data: null,
        table: null,
    };


    componentDidMount() {
        this.getStudentsList();
    }

    getStudentsList = () => {
        let token = localStorage.getItem('token');
        fetch("http://64.225.116.47:3000/api/classes", {
            headers: {
                'Content-Type': 'application/json',
                'token': token
            }
        })
            .then(response => response.json())
            .then(data => {
                    this.setState({data: data.filter(i => i.status === true)});
                    const planing =
                        {
                            monday: [],
                            tuesday: [],
                            wednesday: [],
                            thursday: [],
                            friday: [],
                            saturday: [],
                            sunday: []
                        }
                    ;
                    this.state.data.map(item => {
                        debugger
                        for (let plan of item.classLessonPlaning) {
                            switch (plan.name) {
                                case 'monday':
                                    planing.monday.push({classNAME: item.className, start: plan.start, finish: plan.finish})
                                    break;
                                case 'tuesday':
                                    planing.tuesday.push({
                                        classNAME: item.className,
                                        start: plan.start,
                                        finish: plan.finish
                                    })
                                    break;
                                case 'wednesday':
                                    planing.wednesday.push({
                                        classNAME: item.className,
                                        start: plan.start,
                                        finish: plan.finish
                                    })
                                    break;
                                case 'thursday':
                                    planing.thursday.push({
                                        classNAME: item.className,
                                        start: plan.start,
                                        finish: plan.finish
                                    })
                                    break;
                                case 'friday':
                                    planing.friday.push({classNAME: item.className, start: plan.start, finish: plan.finish})
                                    break;
                                case 'saturday':
                                    planing.saturday.push({
                                        classNAME: item.className,
                                        start: plan.start,
                                        finish: plan.finish
                                    })
                                    break;
                                case 'sunday':
                                    planing.sunday.push({classNAME: item.className, start: plan.start, finish: plan.finish})
                                    break;
                            }
                            console.log(planing);
                        }
                        this.setState({table: planing})
                    })
                }
            )

    };

    render() {
        const table = this.state.table;

        return (
            <div>
                {table ?
                    <Row>
                        <Col lg={3}>
                            <Row>
                                <Col>
                                    <p className={'day-title'}>Pazartesi</p>
                                </Col>
                            </Row>
                            <Row>
                                <Col>
                                    {
                                        table.monday.map(item => (
                                            <div className={'box-one monday-color'}>
                                                <div>
                                                    <Icon type="number"/>
                                                    {item.classNAME}
                                                </div>
                                                <div>
                                                    <Icon type="clock-circle"/>
                                                    {item.start}
                                                </div>
                                                <div>
                                                    <Icon type="clock-circle"/>
                                                    {item.finish}
                                                </div>
                                            </div>

                                        ))
                                    }
                                </Col>
                            </Row>
                        </Col>
                        <Col lg={3}>
                            <Row>
                                <Col>
                                    <p className={'day-title'}>Salı</p>
                                </Col>
                            </Row>
                            <Row>
                                <Col>
                                    {
                                        table.tuesday.map(item => (
                                            <div className={'box-one tuesday-color'}>
                                                <div>
                                                    <Icon type="number"/>
                                                    {item.classNAME}
                                                </div>
                                                <div>
                                                    <Icon type="clock-circle"/>
                                                    {item.start}
                                                </div>
                                                <div>
                                                    <Icon type="clock-circle"/>
                                                    {item.finish}
                                                </div>
                                            </div>

                                        ))
                                    }
                                </Col>
                            </Row>
                        </Col>
                        <Col lg={3}>
                            <Row>
                                <Col>
                                    <p className={'day-title'}>Çarşamba</p>
                                </Col>
                            </Row>
                            <Row>
                                <Col>
                                    {
                                        table.wednesday.map(item => (
                                            <div className={'box-one wednesday-color'}>
                                                <div>
                                                    <Icon type="number"/>
                                                    {item.classNAME}
                                                </div>
                                                <div>
                                                    <Icon type="clock-circle"/>
                                                    {item.start}
                                                </div>
                                                <div>
                                                    <Icon type="clock-circle"/>
                                                    {item.finish}
                                                </div>
                                            </div>

                                        ))
                                    }
                                </Col>
                            </Row>
                        </Col>
                        <Col lg={3}>
                            <Row>
                                <Col>
                                    <p className={'day-title'}>Perşembe</p>
                                </Col>
                            </Row>
                            <Row>
                                <Col>
                                    {
                                        table.thursday.map(item => (
                                            <div className={'box-one thursday-color'}>
                                                <div>
                                                    <Icon type="number"/>
                                                    {item.classNAME}
                                                </div>
                                                <div>
                                                    <Icon type="clock-circle"/>
                                                    {item.start}
                                                </div>
                                                <div>
                                                    <Icon type="clock-circle"/>
                                                    {item.finish}
                                                </div>
                                            </div>

                                        ))
                                    }
                                </Col>
                            </Row>
                        </Col>
                        <Col lg={3}>
                            <Row>
                                <Col>
                                    <p className={'day-title'}>Cuma</p>
                                </Col>
                            </Row>
                            <Row>
                                <Col>
                                    {
                                        table.friday.map(item => (
                                            <div className={'box-one friday-color'}>
                                                <div>
                                                    <Icon type="number"/>
                                                    {item.classNAME}
                                                </div>
                                                <div>
                                                    <Icon type="clock-circle"/>
                                                    {item.start}
                                                </div>
                                                <div>
                                                    <Icon type="clock-circle"/>
                                                    {item.finish}
                                                </div>
                                            </div>

                                        ))
                                    }
                                </Col>
                            </Row>
                        </Col>
                        <Col lg={3}>
                            <Row>
                                <Col>
                                    <p className={'day-title'}>Cumartesi</p>
                                </Col>
                            </Row>
                            <Row>
                                <Col>
                                    {
                                        table.saturday.map(item => (
                                            <div className={'box-one saturday-color'}>
                                                <div>
                                                    <Icon type="number"/>
                                                    {item.classNAME}
                                                </div>
                                                <div>
                                                    <Icon type="clock-circle"/>
                                                    {item.start}
                                                </div>
                                                <div>
                                                    <Icon type="clock-circle"/>
                                                    {item.finish}
                                                </div>
                                            </div>

                                        ))
                                    }
                                </Col>
                            </Row>
                        </Col>
                        <Col lg={3}>
                            <Row>
                                <Col>
                                    <p className={'day-title'}>Pazar</p>
                                </Col>
                            </Row>
                            <Row>
                                <Col>
                                    {
                                        table.sunday.map(item => (
                                            <div className={'box-one sunday-color'}>
                                                <div>
                                                    <Icon type="number"/>
                                                    {item.classNAME}
                                                </div>
                                                <div>
                                                    <Icon type="clock-circle"/>
                                                    {item.start}
                                                </div>
                                                <div>
                                                    <Icon type="clock-circle"/>
                                                    {item.finish}
                                                </div>
                                            </div>

                                        ))
                                    }
                                </Col>
                            </Row>
                        </Col>

                    </Row>
                    : <div></div>}
            </div>

        );
    }
}

export default Home;
