import React, {Component} from 'react';
import {Descriptions, Button, Table, Input, Modal} from 'antd';

const {Column, ColumnGroup} = Table;

class StudentDetail extends Component {
    state = {
        data: null,
        visible: false,
        fieldModal: "",
        dataModal: ""
    };


    updateInputValue = (evt) => {
        this.setState({
            dataModal: evt.target.value
        });
    };

    showModal = (field, value) => {
        this.setState({
            visible: true,
        });
        this.setState({fieldModal: value});
        this.setState({dataModal: field});
    };

    handleOk = e => {
        this.state.data[this.state.fieldModal] = this.state.dataModal;
        this.updatePaymentService();
        this.setState({
            visible: false,
        });
    };

    handleCancel = e => {
        console.log(e);
        this.setState({
            visible: false,
        });
    };

    componentDidMount() {
        const {id} = this.props.match.params

        let token = localStorage.getItem('token');
        fetch("http://64.225.116.47:3000/api/student/" + id, {
            headers: {
                'Content-Type': 'application/json',
                'token': token
            }
        })
            .then(response => response.json())
            .then(data => this.setState({data: data[0]}));
    }


    pay = (item) => {
        this.state.data.planingInstallment.map(data => {
            if (data.id == item) {
                data.isItPaid = true;
            }
        });
        this.updatePaymentService();
    };

    passiveStudent = () => {
        this.state.data.status = false;
        this.updatePaymentService();
    };

    updatePaymentService = () => {
        let token = localStorage.getItem('token');

        fetch("http://64.225.116.47:3000/api/student/installmentPay/" + this.state.data._id, {
            method: 'PUT',
            headers: {
                'Content-Type': 'application/json',
                'token': token
            },
            // eslint-disable-next-line no-undef
            body: JSON.stringify(this.state.data)
        }).then(function (res) {
            return res, res.json()
        })
            .then(function (data) {
                this.props.navigation.navigate("/students-list")
            }).catch(function (err) {
            console.log(err)
        })
    };

    render() {
        const user = this.state.data;
        return (
            <div>
                <div style={{textAlign: 'right'}}>
                    <Button type="danger" onClick={this.passiveStudent}>Öğrenci Kaydını Sil</Button>
                </div>
                {user ?
                    <Descriptions title="Öğrenci Bilgileri">
                        <Descriptions.Item label="Öğrenci Adı ve Soyadı"><a
                            onClick={this.showModal.bind(this, user.studentName, 'studentName')}> {user.studentName}</a></Descriptions.Item>
                        <Descriptions.Item label="Öğrenci E-Posta"><a
                            onClick={this.showModal.bind(this, user.studentEmail, 'studentEmail')}>{user.studentEmail}</a></Descriptions.Item>
                        <Descriptions.Item label="Öğrenci Telefon">
                            <a
                                onClick={this.showModal.bind(this, user.studentPhone, 'studentPhone')}>
                                {user.studentPhone}</a></Descriptions.Item>
                        <Descriptions.Item label="Öğrenci Adres"><a
                            onClick={this.showModal.bind(this, user.studentAddress, 'studentAddress')}>{user.studentAddress}</a></Descriptions.Item>
                        <Descriptions.Item label="Öğrenci Doğum Tarihi">{user.studentAge}</Descriptions.Item>
                        <Descriptions.Item label="Veli Adı ve Soyadı"><a
                            onClick={this.showModal.bind(this, user.parentName, 'parentName')}>{user.parentName}</a></Descriptions.Item>
                        <Descriptions.Item label="Veli E-Posta"><a
                            onClick={this.showModal.bind(this, user.parentEmail, 'parentEmail')}>{user.parentEmail}</a></Descriptions.Item>
                        <Descriptions.Item label="Veli Telefon"><a
                            onClick={this.showModal.bind(this, user.parentPhone, 'parentPhone')}>{user.parentPhone}</a></Descriptions.Item>

                        {user.class ?
                            <Descriptions.Item label="Sınıf">{user.class[0].className}</Descriptions.Item>
                            : <div></div>}
                        {user.class ?
                            <Descriptions.Item label="Dönem">{user.class[0].classDate}</Descriptions.Item>
                        : <div></div>}
                        <Descriptions.Item label="Ödeme Türü">{user.payment}</Descriptions.Item>
                        {user.intallmentPayment ?
                            <Descriptions.Item label="Taksit Ödemesi">{user.installmentPayment}</Descriptions.Item>
                            :
                            <div></div>
                        }
                        {user.totalPayment ?
                            <Descriptions.Item label="Nakit Ödenen Tutar">{user.totalPayment}</Descriptions.Item>
                            :
                            <div></div>
                        }
                        {user.installmentCount ?
                            <Descriptions.Item label="Taksit Sayısı">{user.installmentCount}</Descriptions.Item> :
                            <div></div>}
                        {user.intallmentPaymentDate ? <Descriptions.Item
                                label="Taksit Ödemesi Başlangıç">{user.installmentPaymentDate}</Descriptions.Item> :
                            <div></div>}
                        <Descriptions.Item label="Kayıt Tarihi">
                            {user.studentCreatedDate}</Descriptions.Item>
                    </Descriptions>
                    : <div>Yükleniyor..</div>}


                {user ?
                    <Table dataSource={user.planingInstallment}>
                        <ColumnGroup title="Taksit">
                            <Column title="Sayı" dataIndex="installNumber" key="installNumber"/>
                            <Column title="Tutar" dataIndex="installPay" key="installPay"/>
                        </ColumnGroup>
                        <Column title="Son Ödeme Tarihi" dataIndex="installmentDate" key="installmentDate"/>
                        <Column title="Durum" dataIndex="isItPaid" key="isItPaid"
                                render={(text, record) => (
                                    <span>
                                        {
                                            record.isItPaid ? 'Ödendi' :
                                                <Button type="primary"
                                                        onClick={this.pay.bind(this, record.id)}>Öde</Button>
                                        }
                                    </span>
                                )}
                        />
                    </Table>
                    : <div>Yükleniyor..</div>}


                <Modal
                    title="Düzenle"
                    visible={this.state.visible}
                    onOk={this.handleOk}
                    onCancel={this.handleCancel}
                >
                    <Input placeholder={this.state.fieldModal} value={this.state.dataModal}
                           onChange={this.updateInputValue}/>

                </Modal>
            </div>
        );
    }
}

export default StudentDetail;
